<template>
  <div>
    <Spinner v-if="isLoading" />
    <template v-else>
      <div class="container-fluid admin_layout">
        <div class="row">
          <div class="navbar col-md-2 col-10 d-flex flex-column">
            <AdminNavbar />
          </div>

          <div class="coupon col-md-8 col-11 bg-light p-1">
            <div class="card my-5">
              <table class="table">
                <thead>
                  <tr style="background-color: #0085a5; color: white;">
                    <th scope="col">#</th>
                    <th scope="col">序號</th>
                    <th scope="col" class="type">種類</th>
                    <th scope="col" class="store-id">商城id</th>
                    <th scope="col" class="description">描述</th>
                    <th scope="col">折扣</th>
                    <th scope="col">到期日</th>
                    <th scope="col" class="expired">是否過期</th>
                    <th scope="col">編輯</th>
                  </tr>
                </thead>
                <tbody v-for="coupon in coupons" :key="coupon.id">
                  <tr>
                    <th scope="row">
                      <p class="mt-2">{{coupon.id}}</p>
                    </th>

                    <td>
                      <p class="mt-2">{{coupon.couponCode}}</p>
                    </td>

                    <td class="type">
                      <p class="mt-2">{{coupon.Coupon_type.couponType}}</p>
                    </td>

                    <td class="store-id">
                      <p class="mt-2 ml-3">{{coupon.StoreId}}</p>
                    </td>

                    <td class="description">
                      <p class="mt-2">{{coupon.description}}</p>
                    </td>

                    <td>
                      <p class="mt-2">{{coupon.discount}}</p>
                    </td>

                    <td>
                      <p class="mt-2">{{coupon.expireDate | date}}</p>
                    </td>

                    <td v-if="coupon.available === false" class="expired">
                      <p class="mt-2 ml-2">已過期</p>
                    </td>

                    <td v-else class="expired">
                      <p class="mt-2 ml-2">未過期</p>
                    </td>

                    <td>
                      <router-link
                        :to="{name: 'AdminCouponEdit', params: { id: coupon.id}}"
                        class="btn btn-link"
                      >
                        <i class="fas fa-edit" style="color: #0085a5;"></i>
                      </router-link>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import AdminNavbar from "./../components/AdminNavbar";
import { dateFilter } from "./../utils/mixins";
import AdminAPI from "./../apis/admin";
import Spinner from "./../components/Spinner";
import { Toast } from "./../utils/helpers";
import { mapState } from "vuex";

export default {
  mixins: [dateFilter],
  components: {
    AdminNavbar,
    Spinner
  },
  data() {
    return {
      coupons: [],
      expireDate: "",
      isLoading: true
    };
  },
  computed: {
    ...mapState(["currentUser", "isAuthenticated"])
  },
  created() {
    this.fetchAdminCoupon();
  },
  methods: {
    async fetchAdminCoupon() {
      try {
        const { data, statusText } = await AdminAPI.getCouponManagePage();

        if (statusText !== "OK") {
          throw new Error(statusText);
        }

        this.coupons = data.coupons;

        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        Toast.fire({
          type: "error",
          title: "無法取得折價券資料，請稍後再試"
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@mixin respond-between($lower, $upper, $font-size) {
  @media screen and (min-width: $lower) and (max-width: $upper) {
    font-size: $font-size;
  }
}

@mixin respond-and($upper) {
  @media screen and (max-width: $upper) {
    @content;
  }
}

.container-fluid {
  margin-top: -92px;
  margin-left: -89px;
  width: 115%;
  @include respond-and(768px) {
    margin-top: 0px;
    margin-left: -60px;
    margin-bottom: 150px;
  }
}

.coupon {
  margin-left: 60px;
  @include respond-and(768px) {
    margin-left: 0px;
  }
}

.card,
.btn {
  @include respond-between(960px, 1100px, 15px);
  @include respond-between(768px, 960px, 10px);
  @include respond-and(768px) {
    font-size: 10px;
  }
}

p {
  @include respond-between(960px, 1100px, 13px);
  @include respond-between(768px, 960px, 10px);
  @include respond-and(768px) {
    font-size: 10px;
  }
}

.description,
.store-id,
.expired,
.type {
  @include respond-and(768px) {
    display: none;
  }
}

.navbar {
  @include respond-and(768px) {
    margin-left: 25px;
  }
}
</style>

